<i18n>
{
  "en": {
    "AboutUs": "About us",
    "ServiceTelephone": "Service telephone"
  },
  "zh": {
    "AboutUs": "关于我们",
    "ServiceTelephone": "客服电话"
  }
}
</i18n>

<template>
  <div class="about">
    <head-top :title='$t("AboutUs")' :go-back="true" @goBackHander="goBackHander"></head-top>
    <div class="about-container">
      <div class="logo">
        <span>
          <img src="../../assets/global_images/logo.png" alt="">
        </span>
        <strong>INCHARGE</strong>
        <em>v{{version}}</em>
      </div>
      <div class="phone">
        <span>
          <img src="../../assets/sidebar_images/phone.png" alt="">
          {{$t("ServiceTelephone")}}
        </span>
        <strong>020-37268390</strong>
      </div>
    </div>
  </div>
</template>

<script>
import headTop from '../../components/Header/index'
export default {
  name: 'about',
  data () {
    return {
      version: '0.0.0'
    }
  },

  mounted () {
    this.dataInteractionResponse(data => {
      // 版本号返回
      if (data.msgType === '0x5027') {
        if (data.result) {
          this.version = data.data.version
        }
      }
    })
    // 版本号请求
    this.dataInteractionRequest({
      msgType: '0x5026'
    })
  },

  components: {
    headTop
  },

  methods: {
    goBackHander () {
      // console.log(123)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.about{
  .fs;
  background: #FFF;
  .about-container{
    .fs-noTop;
    color: #585858;
    .logo{
      .wh(700px, 500px);
      border-radius: 20px;
      margin: 30px auto;
      box-shadow: 0 4px 40px -10px rgba(0, 0, 0, .2);
      padding-top: 45px;
      span{
        .wh(170px, 170px);
        display: block;
        border-radius: 50%;
        margin: 0 auto 44px auto;
        overflow: hidden;
        img{
          height: 100%;
          // width: 100%;
        }
      }
      strong{
        // .wh(200px, 44px);
        margin: 0 auto;
        font-size: 60px;
        display: block;
        font-weight: bold;
      }
      em{
        font-size: 36px;
        display: block;
        margin-top: 10px;
      }
    }
    .phone{
      .wh-l(700px, 100px);
      border-radius: 20px;
      margin: 30px auto;
      box-shadow: 0 4px 40px -10px rgba(0, 0, 0, .2);
      padding: 0 25px;
      span{
        .wh(70%, 100%);
        display: block;
        float: left;
        font-size: 36px;
        text-align: left;
        img{
          height: 32px;
          position: relative;
          top: 2px;
          margin-right: 10px;
        }
      }
      strong{
        .wh(30%, 100%);
        display: block;
        float: left;
        text-align: right;
        font-size: 24px;
      }
    }
  }
}
</style>



